@font-face {
    font-family: 'brema';
    src: url('../../assets//fonts/brema.ttf');
}

video{
    z-index: -1;
    position: fixed;
    object-fit: cover;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.heroContainer{
    display: flex;
    height: 100vh;
    width: 100%;
    flex-direction: column;
    align-items: center;
    ;
}

.heroContainer  h1{
    color: black;
    font-size: 100px;
    margin-top: -100;
    padding-top: 10%;
    font-family: brema;

}

.socials{
    display: flex;
    justify-content: center;
    align-items: center;
}

.socials li{
    list-style: none;
    padding: 0 10px;
}

.social-links img{
    height: 30px;
    width: auto;
    transition: transform .2s;
}

.social-links img:hover{
    transform: scale(1.2);
}

@media screen and (max-width: 800px) {
    .heroContainer  h1{
        font-size: 70px;
        
    }
}
