@font-face {
    font-family: 'brema';
    src: url('../../assets//fonts/brema.ttf');
}
@font-face {
    font-family: 'Cotham Sans';
    src: url('../../assets/fonts/CothamSans.otf');
}
@font-face {
    font-family: 'roboto';
    src: url('../../assets/fonts/Roboto-Regular.ttf');
}

.aboutContainer{
    display: flex;
    flex-direction: column;
    padding: 50px 100px;
    position: relative;
    background-color: beige;
    height: 100vh;
}

.profileInfo{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.profileTextWrapper{
    flex: 20%;
    position: relative;
    padding: 0 60px;
    background-color: beige;
}

.profileTextWrapper h1{
    font-size: 5rem;
    font-family: Brema;
}

.profileTextWrapper p{
    font-size: 20px;
    font-family: Cotham Sans;
}


.profileImgWrapper img{
    max-width: 600px;
    width: 100%;
    height: auto;
}
